.map {
  display: flex;
  padding-block-start: 207px;
  justify-content: center;
  padding-inline: 100px;
}
.map p {
  margin: 0;
}
@media (max-width: 992.99px) {
  .map {
    padding-inline: 30px;
    padding-block-start: 100px;
  }
}

.map__body {
  width: 100%;
  max-width: 1293px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 992.99px) {
  .map__body {
    flex-direction: column;
    align-items: center;
    gap: 80px;
  }
}

.map__title {
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 45px;
  line-height: 100%;
}
@media (max-width: 992.99px) {
  .map__title {
    text-align: center;
  }
}

.map__number {
  display: flex;
  align-items: center;
  padding-block-start: 64px;
  margin-inline-start: 2px;
}
@media (max-width: 992.99px) {
  .map__number {
    flex-direction: column;
    gap: 15px;
  }
}

.map__number-number {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 100%;
  font-weight: 500;
  padding-inline-start: 16px;
  line-height: 25px;
}
@media (max-width: 992.99px) {
  .map__number-number {
    padding-inline-start: 0px;
  }
}

.map__social-background {
  display: flex;
  gap: 10px;
  padding-inline-start: 49px;
}
@media (max-width: 992.99px) {
  .map__social-background {
    padding-inline-start: 0px;
  }
}

.map__social-img {
  background-color: #337d5a;
  padding: 8px;
  object-fit: cover;
  border-radius: 50px;
}

.map__email {
  display: flex;
  align-items: center;
  padding-block-start: 43px;
  margin-inline-start: 2px;
}
@media (max-width: 992.99px) {
  .map__email {
    justify-content: center;
  }
}

.map__email-text {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 100%;
  font-weight: 500;
  padding-inline-start: 15px;
}

.map__office {
  display: flex;
  align-items: start;
  padding-block-start: 51px;
  margin-inline-start: 2px;
}
@media (max-width: 992.99px) {
  .map__office {
    padding-block-start: 20px;
    justify-content: center;
  }
}

.office__title {
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 17px;
  line-height: 100%;
  padding-inline-start: 14px;
}

.office__text {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 100%;
  font-weight: 500;
  max-width: 180px;
  line-height: 21px;
  padding-inline-start: 32px;
  padding-block-start: 11px;
}
@media (max-width: 992.99px) {
  .office__text {
    max-width: 100%;
    text-align: center;
  }
}
@media (max-width: 360px) {
  .office__text {
    padding-inline-start: 0px;
  }
}

.map__tree {
  display: flex;
  padding-block-start: 39px;
  margin-inline-start: 2px;
}
@media (max-width: 992.99px) {
  .map__tree {
    justify-content: center;
  }
}

.tree__img {
  width: 16px;
  height: 18.65px;
}

.tree__title {
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 17px;
  line-height: 100%;
  font-weight: 800;
  padding-inline-start: 15px;
}

.tree__text {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 100%;
  font-weight: 500;
  line-height: 21px;
  padding-inline-start: 32px;
  padding-block-start: 10px;
}
@media (max-width: 992.99px) {
  .tree__text {
    text-align: center;
  }
}

.google__map {
  margin-block-start: -17px;
}
@media (max-width: 1300px) {
  .google__map {
    width: 450px;
  }
}
@media (max-width: 600px) {
  .google__map {
    width: 300px;
  }
}
@media (max-width: 414px) {
  .google__map {
    width: 280px;
  }
}